main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

:root {
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  background-color: grey;
  color: black;
  font-family: 'Courier New', Courier, monospace;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  color: black;
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
  justify-content: center;
}

.row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
}

a {
  text-decoration: none;
  font-weight: bold;
  border-radius: 5px;
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

a:hover {
  text-decoration: underline;
}

header {
  background-image: url(../src/assets/images/hero.png);
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  text-align: center;
}

header a {
  color: pink;
}

button {
  background: lightseagreen;
  border: 0;
  border-radius: 0.5rem;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 15px;
  font-size: 1rem;
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

span[role='img'] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid grey;
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
  width: 600px;
}

input:focus {
  border: 1px darkblue;
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

ol li {
  list-style: inside;
}

ul li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  color: black;
  cursor: pointer;
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  justify-content: space-evenly;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

.navActive {
  color: peachpuff;
}

.footer {
  background-image: url(../src/assets/images/hero.png);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-top: 150px;
}

.photo {
  width: auto;
  height: auto;
  max-width: 480px;
  max-height: 640px;

  margin-bottom: 50px;
}

.image {
  width: auto;
  height: auto;
  max-width: 500px;
  max-height: 500px;
  padding: 50px;
}

.logo {
  width: 50px;
  height: 50px;
  margin-bottom: 50px;
  cursor: pointer;
}

.logo:hover {
  opacity: 0.5;
}

.center {
  display: flex;
  justify-content: center;
}

.page-header {
  font-family: 'Roboto', sans-serif;
  font-size: 2.5em;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
  padding-bottom: 2rem;
}

.bottom-spacing {
  margin-bottom: var(--spacing-five);
}

.padding {
  padding: 50px;
}

/* Viewports Styling start*/
@media screen and (max-width: 980px) {
}

@media screen and (max-width: 817px) {
  header div {
    position: relative;
    width: 110%;
  }

  header div h2 {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
  }
  .row {
    display: block;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 0 100px 0 auto;
  }

  h2 {
    font-size: 1.5rem;
  }

  nav ul li span {
    font-size: 0.75em;
  }

  .mobile-header {
    width: 110%;
  }

  .mobile-view {
    display: inline;
    text-align: end;
  }

  .photo {
    width: auto;
    height: auto;
    max-width: 500px;
    max-height: 300px;
    padding: 50px;
  }

  input {
    width: 350px;
  }

  textarea {
    width: 350px;
    height: 120px;
    border: 3px solid blue;
    padding: 5px;
  }

  ol {
    font-size: 0.75rem;
  }
}

@media screen and (max-width: 768px) {
}

@media screen and (max-width: 575px) {
}

/* Viewports Styling end*/
